@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap');

:root {
    font-size: 62.5%;
    --color-1: black;
    --color-2: white;
    --color-3: #0ed180;
}

* {
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
}

body {
    width: 100dvw;
    height: 100dvh;
    overflow: hidden;
    background-color: var(--color-1);
}

span {
    line-height: 1.5em;
}

strong {
    color: var(--color-3);
}

button {
    cursor: pointer;
}

.info-overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
    background-color: color-mix(in srgb, var(--color-1), transparent 20%);
    transition: opacity 0.25s ease-in-out;
    backdrop-filter: blur(1rem);

    &.close {
        opacity: 0;
        pointer-events: none;

        .info-container {
            transform: scale(0.85);
        }
    }

    .info-container {
        padding: 4rem;
        display: flex;
        flex-direction: column;
        gap: 4rem;
        background: var(--color-1);
        transition: transform 0.25s ease-in-out;
        border: 0.2rem solid var(--color-2);

        .main {
            display: flex;
            flex-direction: column;
            gap: 2rem;

            .title {
                font-size: 3rem;
                font-weight: 600;
                color: var(--color-2);
            }

            .description {
                font-size: 1.8rem;
                font-weight: 400;
                color: var(--color-2);
            }

            .author {
                font-size: 1.4rem;
                font-weight: 400;
                color: var(--color-2);
            }
        }

        .tutorial {
            .text {
                font-size: 1.4rem;
                font-weight: 400;
                color: var(--color-2);
            }
        }

        .button {
            width: 100%;
            height: 6rem;
            display: flex;
            justify-content: center;
            align-items: center;

            .text {
                font-size: 2rem;
                font-weight: 600;
                background: var(--color-2);
                color: var(--color-1);
            }
        }
    }
}

.controls {
    position: fixed;
    left: 0;
    bottom: 0;
    margin: 1rem;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background: color-mix(in srgb, var(--color-1), transparent 20%);

    .line {
        display: flex;
        align-items: center;
        gap: 1rem;
        font-size: 1.2rem;
        font-weight: 500;
        color: var(--color-2);

        strong {
            display: flex;
            justify-content: center;
            width: 2rem;
            font-weight: 800;
        }
    }
}