@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap";
:root {
  --color-1: black;
  --color-2: white;
  --color-3: #0ed180;
  font-size: 62.5%;
}

* {
  box-sizing: border-box;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  background-color: var(--color-1);
  width: 100dvw;
  height: 100dvh;
  overflow: hidden;
}

span {
  line-height: 1.5em;
}

strong {
  color: var(--color-3);
}

button {
  cursor: pointer;
}

.info-overlay {
  z-index: 2;
  background-color: color-mix(in srgb, var(--color-1), transparent 20%);
  backdrop-filter: blur(1rem);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: opacity .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;

  &.close {
    opacity: 0;
    pointer-events: none;

    & .info-container {
      transform: scale(.85);
    }
  }

  & .info-container {
    background: var(--color-1);
    border: .2rem solid var(--color-2);
    flex-direction: column;
    gap: 4rem;
    padding: 4rem;
    transition: transform .25s ease-in-out;
    display: flex;

    & .main {
      flex-direction: column;
      gap: 2rem;
      display: flex;

      & .title {
        color: var(--color-2);
        font-size: 3rem;
        font-weight: 600;
      }

      & .description {
        color: var(--color-2);
        font-size: 1.8rem;
        font-weight: 400;
      }

      & .author {
        color: var(--color-2);
        font-size: 1.4rem;
        font-weight: 400;
      }
    }

    & .tutorial {
      & .text {
        color: var(--color-2);
        font-size: 1.4rem;
        font-weight: 400;
      }
    }

    & .button {
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 6rem;
      display: flex;

      & .text {
        background: var(--color-2);
        color: var(--color-1);
        font-size: 2rem;
        font-weight: 600;
      }
    }
  }
}

.controls {
  background: color-mix(in srgb, var(--color-1), transparent 20%);
  flex-direction: column;
  gap: 1rem;
  margin: 1rem;
  padding: 2rem;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;

  & .line {
    color: var(--color-2);
    align-items: center;
    gap: 1rem;
    font-size: 1.2rem;
    font-weight: 500;
    display: flex;

    & strong {
      justify-content: center;
      width: 2rem;
      font-weight: 800;
      display: flex;
    }
  }
}
